import * as React from "react"
import {PageProps, HeadFC, Link} from "gatsby";
import Layout from "../components/layout";
import {formatDate} from "../utils/date";
import {SignupForm} from "../components/signup-form";


interface CodelabsPageProps {
    labs: any
}

const CodelabsPage: React.FC<PageProps<{}, CodelabsPageProps>> = ({ pageContext }) => {

    const { labs } = pageContext

    return (
        <Layout>
            <ul className={"p-0"}>
                {labs.map((lab: any) => (
                    <li style={{display: "block"}} key={lab.slug}>
                        <Link style={{ boxShadow: `none` }} to={`/codelab/${lab.slug}`}>
                            <h2 style={{ marginBottom: `0.2rem` }}>
                                {lab.title}
                            </h2>
                        </Link>
                        <small>{formatDate(lab.publishedAt)}</small>
                        <p style={{ marginTop: `0.5rem` }}>{lab.brief}</p>
                    </li>
                ))}
            </ul>
            <br />
            <SignupForm />
            <br />
        </Layout>
    )
}

export const Head: HeadFC = () => <title>Codelabs | xprilion&apos;s blog</title>

export default CodelabsPage